}

.no-margin {
	margin: 0px;
}

.no-margin-left {
	margin-left: 0px;
}

.no-margin-right {
	margin-right: 0px;
}

@media screen and (max-width: 510px) {
	.row {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.col-sm {
		flex: 12;
	}
}

@media screen and (max-width: 690px) {
	.row {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.col-md {
		flex: 12;
	}
}

@media screen and (max-width: 960px) {
	.row {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.col-lg {
		flex: 12;
	}
}

@media screen and (max-width: 1280px) {
	.row {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.col-xl {
		flex: 12;
	}
}

.align-items-center {
	align-items: center;
}

.align-items-top {
	align-items: top;
}

.align-items-bottom {
	align-items: bottom;
}

.justify-content-center {
	justify-content: center;
}

.justify-content-left {
	justify-content: left;
}

.justify-content-right {
	justify-content: right;
}

.column {
